export default {
	data() {
		return {
			currentTab2: 'all',
			datas: [],
			pages: 0,
		}
	},
	onLoad(options) {
		var that = this;
		if (options.types) {
			that.currentTab2 = options.types;
		}
		that.getOrders();
	},
	methods: {
		// 确认收货
		shouhuo(e) {
			var that = this;
			uni.showModal({
				title: "温馨提示",
				content: "是否确认收货?",
				success(res) {
					if (res.confirm) {
						console.log('确认收货')
						var oid = e.currentTarget.dataset.orderid;
						that.$http.post("orders/confirm_receipt", {
							oid: oid
						}).then((res) => {
							console.log(res.data);
							if (res.code == 1) {
								uni.showToast({
									title: '收货完成',
									duration: 2000,
									success(res) {
										setTimeout(function() {
											that.pages = 0;
											that.datas = [];
											that.getOrders();
										}, 2000)

									},
								})
							}
						})
					}
				},
			})

		},
		// 获取订单
		getOrders() {
			console.log('请求订单')
			var that = this;
			if (that.currentTab2 == 'all') {
				var status = null;
			} else {
				var status = that.currentTab2;
			}
			console.log("pages==", that.pages)
			this.$http.post("orders/index", {
				pages: that.pages,
				status: status
			}).then((res) => {
				console.log("打印订单", res.data);
				that.pages += 1;
				that.datas = that.datas.concat(res.data);
			})
		},
		clickTab2(e) {
			var that = this;
			if (this.currentTab2 === e.target.dataset.current) {
				return false;
			} else {
				that.pages = 0;
				that.datas = [];
				that.currentTab2 = e.target.dataset.current
			}
			that.getOrders();
		},

		jumpTo(e) {
			var jumptype = e.currentTarget.dataset.jumptype;
			var url = e.currentTarget.dataset.url;
			var closepage = e.currentTarget.dataset.closepage;
			this.jumpFunction(jumptype, url, closepage);
		},
	}
}
